.presentation__container {
  width: @mobile-content-inner;

  @media (min-width: @tablet-width) {
    width: @tablet-content;
  }

  @media (min-width: @desktop-width) {
    width: 800px;
  }
}

.presentation__title-container {
  padding: 43px 2px 38px 2px;

  @media (min-width: @tablet-width) {
    width: 463px;
    margin: 0 auto;
    padding-top: 58px;
    padding-bottom: 82px;
  }

  @media (min-width: @desktop-width) {
    padding: 59px 0 85px 0;
  }
}

.presentation__title {
  .text-title;
  .title;
  margin: 0;
  margin-bottom: 27px;

  @media (min-width: @tablet-width) {
    .text-extra-big;
    margin-bottom: 33px;
  }
}

.presentation__description {
  margin: 0;
}

.presentation__image-container {
  position: relative;
  width: @mobile-content-inner;
  height: 177px;
  background-color: @background-image-color;

  @media (min-width: @tablet-width) {
    width: @tablet-content;
    height: 500px;
  }

  @media (min-width: @desktop-width) {
    width: 800px;
  }
}

.presentation__image {
  .images;
  width: 100%;
  height: auto;
}

.presentation__button {
  position: absolute;
  background-image: url("../img/video-button-icon.svg");
  width: 48px;
  height: 54px;
  background-color: transparent;
  border: none;
  top: 50%;
  margin-top: -27px;
  right: 50%;
  margin-right: -24px;
  padding: 0;
  cursor: pointer;
}
